<template>
  <div name="ta2">
    <expandable-section-std title="TA2" :closedOnStart="false" ref="expandTa2">
      <div class="row">
        <div class="col-sm-6">
          <div class="slds-border_bottom topic2">
            <label>TA Mandatory</label>
          </div>
          <div v-for="mappingDefault in taManMappingDefault" class="row">
            <div class="col-sm-6">
              <input-text-std v-model="mappingDefault.TA2_Indicator__c" label="Indicator / Name" disabled class="fontInput"></input-text-std>
            </div>
            <div class="col-sm-6">
              <input-text-std v-model="mappingDefault.TA2_Expert_Field__c" label="Expert field" disabled class="fontInput"></input-text-std>
            </div>
          </div>
        </div>
        <div class="col-sm-6">
          <div class="slds-border_bottom topic2">
            <label>Additional TA2</label>
          </div>
          <div v-for="(mappingOptional, index) in taManMappingOptional" class="row" :key="index">
            <div class="col-sm-6">
              <combobox-std
                v-model="mappingOptional.TA2_Indicator__c"
                :options="taManMasterOption(index)"
                label="Indicator / Name"
                disabled
                :classCustom="'font-size: 11px;'"
              />
            </div>
            <div class="col-sm-6">
              <input-text-std v-model="mappingOptional.TA2_Expert_Field__c" label="Expert field" disabled class="fontInput"></input-text-std>
            </div>
          </div>
        </div>
      </div>
<!--       <div class="row">
        <div class="col-md-12">
          <checkbox-std
            v-model="emoc.TA_Selection_confirmed__c"
            label="TA Selection confirmed"
            :inline="true"
            disabled
          />
        </div>
      </div> -->
    </expandable-section-std>
  </div>
</template>
<script>
export default {
  name: 'ta2',
  props: ['emoc'],
  created () {
    this.$store.dispatch('receiveTaManMapping', this.emoc.Id)
    this.$store.dispatch('receiveTaManMaster', this.emoc.Id)
  },
  computed: {
    taManMaster () { return this.$store.state.taManMaster },
    taManMapping () { return this.$store.state.taManMapping },
    taManMappingDefault () {
      // console.log('taManMapping', this.taManMapping)
      return this.taManMapping.filter(df => {
        return df.Require__c === 'Require'
      })
    },
    taManMappingOptional () {
      return this.taManMapping.filter(df => {
        return df.Require__c === 'Optional'
      })
    }
  },
  methods: {
    taManMasterOption (index) {
      let retOption = this.taManMaster.filter(df => {
        return this.taManMappingOptional.map(dm => {
          return dm.TA2_Indicator__c
        }).indexOf(df.TA2_Indicator__c) === -1 || this.taManMappingOptional[index].TA2_Indicator__c === df.TA2_Indicator__c
      }).map(dm => {
        return {value: dm.TA2_Indicator__c, label: dm.TA2_Indicator__c}
      })
      return retOption
    }
  }
}
</script>
<style scoped>
  .fontInput {
    font-size: 11px;
  }
</style>



