<template>
  <div name="extension">
    <expandable-section-std title="Extension?" ref="expandExtension" :closedOnStart="false">
      <div class="data-frame">
        <div class="row">
          <div class="col-md-6 col-sm-6">
            <checkbox-std
              v-model="emoc.Extend_Require_to_extend__c"
              label="Extend"
              name="Extend_Require_to_extend__c"
              :inline="true"
              disabled
            />
 <!--            <textarea-std
              v-model="emoc.Reason_for_Extension_temporary_MOC__c"
              name="Reason_for_Extension_temporary_MOC__c"
              label="Reason of extension"
              :rows="1"
              disabled
            >
              <template #error>
                <span>{{ errors.first('Reason_for_Extension_temporary_MOC__c') }}</span>
              </template>
            </textarea-std> -->
            
          </div>
          <div class="col-md-6 col-sm-6">
            <checkbox-std
              v-model="emoc.Not_Extend_Require_to_extend__c"
              label="Not Extend"
              :inline="true"
              disabled
            />
            <div v-if="showClearanceNumber">
              <lookups-std v-model="emoc.Clearance_Number_Final_Group_C__c"
                           label="Reference clearance certificate number for return the change back to normal"
                           placeholder="Search..."
                           :options="optionsLookupClearanceNumber"
                           name="Clearance_Number_Final_Group_C__c"
                           @search="filterLookupClearanceNumber(arguments[0])"
                           icon="custom"
                           disabled
                           loading
                           :required="setShowRequired" >
              </lookups-std>
            </div>
          </div>
        </div>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>

export default {
  name: 'extension',
  props: [
    'emoc'
  ],
  data () {
    return {
      nameRules: [
        v => !!v || 'Name is required'
      ]
    }
  },
  created () {
    if (this.emoc.Not_Extend_Require_to_extend__c && (this.emoc.MOS_for_maintenance__c || this.emoc.Safeguarding_force_signal__c)) {
      this.filterLookupClearanceNumber(this.emoc.Clearance_Number_Final_Group_C__c)
    }
  },
  computed: {
    setRuleExten () {
      return (this.emoc.Extend_Require_to_extend__c === true) ? 'required' : ''
    },
    lookupClearanceNumber () { return this.$store.state.lookupClearanceNumber },
    optionsLookupClearanceNumber () {
      return this.lookupClearanceNumber.map(dm => {
        return {
          value: dm.Work_Clearance__c,
          label: dm.Work_Clearance__c,
          icon: 'custom'
        }
      })
    },
    showClearanceNumber () {
      if (this.emoc.Not_Extend_Require_to_extend__c && (this.emoc.MOS_for_maintenance__c || this.emoc.Safeguarding_force_signal__c)) {
        return true
      } else {
        return false
      }
    },
    setShowRequired () {
      return (this.emoc.I_have_checked_and_confirmed_the_change__c === true && this.showClearanceNumber === true)
    }
  },
  watch: {
    'emoc.Extend_Require_to_extend__c' (newValue) {
      if (newValue) {
        this.emoc.Not_Extend_Require_to_extend__c = false
      }
    },
    'emoc.Not_Extend_Require_to_extend__c' (newValue) {
      if (newValue) {
        this.emoc.Extend_Require_to_extend__c = false
      }
    },
    'emoc.Clearance_Number_Final_Group_C__c' (newVal) {
      if (newVal !== '' && newVal) {
        this.filterLookupClearanceNumber(newVal)
      }
    }
  },
  methods: {
    filterLookupClearanceNumber (searchParam) {
      this.$store.dispatch('receiveLookupClearanceNumber', searchParam)
    }
  }
}
</script>


