<template>
  <div class="create-emoc">
      <functional-override :emoc="emoc"
                           :recId="recId"
                           ref="functionalOverride" />
  </div>
</template>
<script>
import functionalOverride from './FunctionalOverride.vue'

export default {
  name: 'create-emoc',
  data () {
    return {
      recId: ''
    }
  },
  created () {
    this.$store.dispatch('receiveLookupFncLocation', { type: 'main', sendParam: '' })
    this.$store.dispatch('receiveLookupUser', { type: 'main', sendParam: '' })
    this.$store.dispatch('receiveLookupClearanceNumber', '')
    this.$store.dispatch('receivePotentialRiskList')
    this.$store.dispatch('receivePotentialConsequenceList')
    this.$store.dispatch('receiveMitigationPrecautionList')
  },
  mounted () {
    const queryString = window.location.search
    const urlParams = new URLSearchParams(queryString)
    this.recId = urlParams.get('id')
    let paramSearch = {
      emocApplicationId: this.recId
    }
    if (this.emocEditEnable) {
      this.$store.dispatch('receiveEmocApplication', paramSearch)
      this.$store.dispatch('receivePotentialConsequence', paramSearch)
      this.$store.dispatch('receiveMitigationPrecaution', paramSearch)
    }
  },
  computed: {
    emoc () { return this.$store.state.emoc },
    emocEditEnable () {
      return this.recId && this.recId !== ''
    }
  },
  components: {
    functionalOverride
  }
}
</script>

<style src="../assets/style.css"></style>


