<template>
  <div name="prelim-risk-assessment">
    <expandable-section-std title="Prelim Risk assessment" ref="expandPrelimRiskAssessment" :closedOnStart="false">
      <div class="data-frame">
        <div class="row">
          <div class="col-6">
     <!--        <combobox-std
              v-model="emoc.Potential_Risk_c__c"
              name="Potential_Risk_c__c"
              :options="potentialRiskList"
              label="Potential risk"
              disabled
            >
            </combobox-std> -->
            <textarea-std
              v-model="emoc.Potential_Risk_c__c"
              name="Potential_Risk_c__c"
              label="Potential risk"
              disabled
              :rows="1"
            >
            </textarea-std>
          </div>
          <div class="col-6">
            <textarea-std
              v-model="emoc.Other__c"
              name="Other__c"
              label="Additional Description of Potential risk"
              disabled
              :rows="1"
            >
            </textarea-std>
          </div>
        </div>

        <div class="row">
          <div class="col-sm-12">
            <div v-for="(potentialCon, index) in potentialConsequenceOp" class="row" :key="index">
              <div class="col-6">
                <div class="row">
                  <div class="col-12 padding-b0" v-if="index === 0">
                    <label class="slds-form-element__label padding-b0" >Potential consequence</label>
                  </div>
         <!--          <div class="col-12 padding-t0" >
                    <combobox-std
                      v-model="potentialCon.Potential_Consequence_c__c"
                      :name="'Potential_Consequence_c__c' + index"
                      :options="potentialConsequenceOption(index)"
                      label=""
                      disabled
                    >
                    </combobox-std>
                  </div> -->
                  <div class="col-12 padding-t0">
                    <textarea-std
                      v-model="potentialCon.Potential_Consequence_c__c"
                      :name="'potentialCon' + index"
                      label=""
                      disabled
                      :rows="2"
                    >
                    </textarea-std>
                  </div>
                </div>
              </div>
              <div class="col-6">
                <div class="row">
                  <div class="col-12 padding-b0" v-if="index === 0">
                    <label class="slds-form-element__label padding-b0" >Additional Description of Potential consequence</label>
                  </div>
                  <div class="col-12 padding-t0">
                    <textarea-std
                      v-model="potentialCon.Other_Potential_Consequence__c"
                      :name="'Other_Potential_Consequence__c' + index"
                      label=""
                      disabled
                      :rows="1"
                    >
                    </textarea-std>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="row">
          <div class="col-sm-12">
            <div v-for="(mitigation, index) in mitigationPrecautionOp" class="row" :key="index">
              <div class="col-6">
                <div class="row">
                  <div class="col-12 padding-b0" v-if="index === 0">
                    <label class="slds-form-element__label padding-b0" >Mitigation/Precaution</label>
                  </div>
           <!--        <div class="col-12 padding-t0" >
                    <combobox-std
                      v-model="mitigation.Mitigation_Precaution_1__c"
                      :name="'Mitigation_Precaution_1__c' + index"
                      :options="mitigationPrecautionOption(index)"
                      label=""
                      disabled
                    >
                    </combobox-std>
                  </div> -->
                  <div class="col-12 padding-t0">
                    <textarea-std
                      v-model="mitigation.Mitigation_Precaution_1__c"
                      :name="'Mitigation_Precaution_1__c' + index"
                      label=""
                      disabled
                      :rows="2"
                    >
                    </textarea-std>
                  </div>
                </div>
              </div>
              <div class="col-6">
                <div class="row">
                  <div class="col-12 padding-b0" v-if="index === 0">
                    <label class="slds-form-element__label padding-b0" >Additional Description of Mitigation/Precaution</label>
                  </div>
                  <div class="col-12 padding-t0">
                    <textarea-std
                      v-model="mitigation.Other_Mitigation__c"
                      :name="'Other_Mitigation__c' + index"
                      label=""
                      disabled
                      :rows="1"
                    >
                    </textarea-std>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>
export default {
  name: 'prelim-risk-assessment',
  props: [
    'emoc'
  ],
  data () {
    return {}
  },
  created () {
  },
  computed: {
    potentialRiskList () { return this.$store.state.potentialRiskList },
    potentialConsequenceList () { return this.$store.state.potentialConsequenceList },
    potentialConsequence () { return this.$store.state.potentialConsequence },
    potentialConsequenceOp () {
      return this.potentialConsequence.map((df, idx) => {
        df['rowId'] = idx
        return df
      })
    },
    mitigationPrecautionList () { return this.$store.state.mitigationPrecautionList },
    mitigationPrecaution () { return this.$store.state.mitigationPrecaution },
    mitigationPrecautionOp () {
      return this.mitigationPrecaution.map((df, idx) => {
        df['rowId'] = idx
        return df
      })
    }
  },
  methods: {
    potentialConsequenceOption (index) {
      let retOption = this.potentialConsequenceList.filter(df => {
        return this.potentialConsequenceOp.map(dm => {
          return dm.Potential_Consequence_c__c
        }).indexOf(df.Potential_Consequence_c__c) === -1 || this.potentialConsequenceOp[index].Potential_Consequence_c__c === df.Potential_Consequence_c__c || df.Potential_Consequence_c__c === 'อื่นๆ'
      }).map(dm => {
        return {value: dm.Potential_Consequence_c__c, label: dm.Potential_Consequence_c__c}
      })
      return retOption
    },
    mitigationPrecautionOption (index) {
      let retOption = this.mitigationPrecautionList.filter(df => {
        return this.mitigationPrecautionOp.map(dm => {
          return dm.Mitigation_Precaution_1__c
        }).indexOf(df.Mitigation_Precaution_1__c) === -1 || this.mitigationPrecautionOp[index].Mitigation_Precaution_1__c === df.Mitigation_Precaution_1__c || df.Mitigation_Precaution_1__c === 'อื่นๆ'
      }).map(dm => {
        return {value: dm.Mitigation_Precaution_1__c, label: dm.Mitigation_Precaution_1__c}
      })
      return retOption
    }
  }
}
</script>
<style scoped>
  .delete-mapping-optional {
    font-size: large;
    color: #0176d3;
  }
  .delete-mapping-optional-disabled {
    color: rgb(234, 0, 30);
  }
  .delete-mapping-optional
    .icon-delete-mapping-optional {
      cursor: pointer;
    }
  .slds-input-has-icon_right .slds-input {
    text-decoration: line-through;
  }
  .padding-b0{
    padding-bottom: 0px;
  }
  .padding-t0{
    padding-top: 0px;
  }
  .padding-rl0{
    padding-left: 0px;
    padding-right: 0px;
  }
</style>


