<template>
  <div name="group">
    <expandable-section-std title="Functional override categories" ref="expandGroup" :closedOnStart="false">
      <div class="data-frame">
        <div v-show="emoc.OOS__c">
          <checkbox-std
              v-model="emoc.OOS__c"
              label="OOS"
              :inline="true"
              class="input-group"
              disabled
          />
        </div>
        <div v-show="emoc.MOS_for_ops_purpose__c">
          <checkbox-std
              v-model="emoc.MOS_for_ops_purpose__c"
              label="MOS for Ops purpose"
              :inline="true"
              class="input-group"
              disabled
          />
        </div>
        <div v-show="emoc.Inhibit_for_process_or_Equipment_S_D__c">
          <checkbox-std
              v-model="emoc.Inhibit_for_process_or_Equipment_S_D__c"
              label="Inhibit for process or equipment S/D"
              :inline="true"
              class="input-group"
              help-text="(Excluded Fire & Gas System)"
              disabled
          />
        </div>
        <div v-show="emoc.Inhibit_alarm_for_maintenance__c">
          <checkbox-std
              v-model="emoc.Inhibit_alarm_for_maintenance__c"
              label="Inhibit alarm for maintenance"
              :inline="true"
              class="input-group"
              disabled
          />
        </div>
        <div v-show="emoc.Disable_Alarm_by_Ops_for_HW__c">
          <checkbox-std
              v-model="emoc.Disable_Alarm_by_Ops_for_HW__c"
              label="Disable alarm by Ops for HW"
              :inline="true"
              class="input-group"
              disabled
          />
        </div>
        <div v-show="emoc.Inhibit_Alarm_request_for_FOX__c">
          <checkbox-std
              v-model="emoc.Inhibit_Alarm_request_for_FOX__c"
              label="Inhibit alarm request for FOX"
              :inline="true"
              class="input-group"
              disabled
          />
        </div>
        <div v-show="emoc.MOS_for_maintenance__c">
          <checkbox-std
              v-model="emoc.MOS_for_maintenance__c"
              label="MOS for maintenance"
              :inline="true"
              class="input-group"
              disabled
          />
        </div>
        <div v-show="emoc.Safeguarding_force_signal__c">
          <checkbox-std
              v-model="emoc.Safeguarding_force_signal__c"
              label="Safeguarding force signal"
              :inline="true"
              class="input-group"
              disabled
          />
        </div>
        <div class="row" v-if="showClearanceNumber">
          <div class="col-6">
            <lookups-std v-model="emoc.Work_Clearance_c__c"
                         label="Reference clearance certificate number"
                         placeholder="Search..."
                         :options="optionsLookupClearanceNumber"
                         name="Work_Clearance_c__c"
                         @search="filterLookupClearanceNumber(arguments[0])"
                         icon="custom"
                         :required="true"
                         disabled
                         loading>
            </lookups-std>
          </div>
        </div>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>
export default {
  name: 'group',
  props: [
    'emoc'
  ],
  created () {
    if (this.emoc.Work_Clearance_c__c !== '' && this.emoc.Work_Clearance_c__c) {
      this.filterLookupClearanceNumber(this.emoc.Work_Clearance_c__c)
    }
  },
  computed: {
    lookupClearanceNumber () { return this.$store.state.lookupClearanceNumber },
    optionsLookupClearanceNumber () {
      return this.lookupClearanceNumber.map(dm => {
        return {
          value: dm.Work_Clearance__c,
          label: dm.Work_Clearance__c,
          icon: 'custom'
        }
      })
    },
    showClearanceNumber () {
      if (this.emoc.Inhibit_for_process_or_Equipment_S_D__c || this.emoc.Inhibit_alarm_for_maintenance__c || this.emoc.Inhibit_Alarm_request_for_FOX__c || this.emoc.MOS_for_maintenance__c || this.emoc.Safeguarding_force_signal__c) {
        return true
      } else {
        return false
      }
    }
  },
  watch: {
    'emoc.OOS__c' (newValue) {
      if (newValue) {
        this.emoc.MOS_for_ops_purpose__c = false
        this.emoc.Inhibit_for_process_or_Equipment_S_D__c = false
        this.emoc.Inhibit_alarm_for_maintenance__c = false
        this.emoc.Disable_Alarm_by_Ops_for_HW__c = false
        this.emoc.Inhibit_Alarm_request_for_FOX__c = false
        this.emoc.MOS_for_maintenance__c = false
        this.emoc.Safeguarding_force_signal__c = false
      }
    },
    'emoc.MOS_for_ops_purpose__c' (newValue) {
      if (newValue) {
        this.emoc.OOS__c = false
        this.emoc.Inhibit_for_process_or_Equipment_S_D__c = false
        this.emoc.Inhibit_alarm_for_maintenance__c = false
        this.emoc.Disable_Alarm_by_Ops_for_HW__c = false
        this.emoc.Inhibit_Alarm_request_for_FOX__c = false
        this.emoc.MOS_for_maintenance__c = false
        this.emoc.Safeguarding_force_signal__c = false
      }
    },
    'emoc.Inhibit_for_process_or_Equipment_S_D__c' (newValue) {
      if (newValue) {
        this.emoc.OOS__c = false
        this.emoc.MOS_for_ops_purpose__c = false
        this.emoc.Inhibit_alarm_for_maintenance__c = false
        this.emoc.Disable_Alarm_by_Ops_for_HW__c = false
        this.emoc.Inhibit_Alarm_request_for_FOX__c = false
        this.emoc.MOS_for_maintenance__c = false
        this.emoc.Safeguarding_force_signal__c = false
      }
    },
    'emoc.Inhibit_alarm_for_maintenance__c' (newValue) {
      if (newValue) {
        this.emoc.OOS__c = false
        this.emoc.MOS_for_ops_purpose__c = false
        this.emoc.Inhibit_for_process_or_Equipment_S_D__c = false
        this.emoc.Disable_Alarm_by_Ops_for_HW__c = false
        this.emoc.Inhibit_Alarm_request_for_FOX__c = false
        this.emoc.MOS_for_maintenance__c = false
        this.emoc.Safeguarding_force_signal__c = false
      }
    },
    'emoc.Disable_Alarm_by_Ops_for_HW__c' (newValue) {
      if (newValue) {
        this.emoc.OOS__c = false
        this.emoc.MOS_for_ops_purpose__c = false
        this.emoc.Inhibit_for_process_or_Equipment_S_D__c = false
        this.emoc.Inhibit_alarm_for_maintenance__c = false
        this.emoc.Inhibit_Alarm_request_for_FOX__c = false
        this.emoc.MOS_for_maintenance__c = false
        this.emoc.Safeguarding_force_signal__c = false
      }
    },
    'emoc.Inhibit_Alarm_request_for_FOX__c' (newValue) {
      if (newValue) {
        this.emoc.OOS__c = false
        this.emoc.MOS_for_ops_purpose__c = false
        this.emoc.Inhibit_for_process_or_Equipment_S_D__c = false
        this.emoc.Inhibit_alarm_for_maintenance__c = false
        this.emoc.Disable_Alarm_by_Ops_for_HW__c = false
        this.emoc.MOS_for_maintenance__c = false
        this.emoc.Safeguarding_force_signal__c = false
      }
    },
    'emoc.MOS_for_maintenance__c' (newValue) {
      if (newValue) {
        this.emoc.OOS__c = false
        this.emoc.MOS_for_ops_purpose__c = false
        this.emoc.Inhibit_for_process_or_Equipment_S_D__c = false
        this.emoc.Inhibit_alarm_for_maintenance__c = false
        this.emoc.Disable_Alarm_by_Ops_for_HW__c = false
        this.emoc.Inhibit_Alarm_request_for_FOX__c = false
        this.emoc.Safeguarding_force_signal__c = false
      }
    },
    'emoc.Safeguarding_force_signal__c' (newValue) {
      if (newValue) {
        this.emoc.OOS__c = false
        this.emoc.MOS_for_ops_purpose__c = false
        this.emoc.Inhibit_for_process_or_Equipment_S_D__c = false
        this.emoc.Inhibit_alarm_for_maintenance__c = false
        this.emoc.Disable_Alarm_by_Ops_for_HW__c = false
        this.emoc.Inhibit_Alarm_request_for_FOX__c = false
        this.emoc.MOS_for_maintenance__c = false
      }
    },
    'emoc.Work_Clearance_c__c' (newVal) {
      if (newVal !== '' && newVal) {
        this.filterLookupClearanceNumber(newVal)
      }
    }
  },
  methods: {
    filterLookupClearanceNumber (searchParam) {
      this.$store.dispatch('receiveLookupClearanceNumber', searchParam)
    }
  }
}
</script>



